.container {
  padding-top: 10%;
  display: flex;
  width: 100%;
  height: 100vh;
  flex-direction: column;
  align-items: center;
  font-size: large;
  background-image: url(../../image/background.png);
}

.link {
  color: rgb(112, 27, 27);
}
.link:hover,
.link:focus {
  padding-left: 5px;
  padding-right: 5px;
  background-color: rgb(112, 27, 27);
  color: rgb(255, 255, 255);
  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  box-shadow: 0px 2px 7px 0px rgb(112, 27, 27);
  border-radius: 5px;
}
